
.planning-item-container {
  text-align: center;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.close {
  position: absolute;
  top: -8px;
  right: -8px;
  z-index: 20;
}

.close-button {
  background-color: red;
  border-radius: 50%;
}


