.root {
  position: absolute;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 20px repeat(6, 1fr) 60px repeat(4, 1fr);
  grid-template-areas:
    "header header header header header header header header header header header header"
    "schooling schooling business business adventure adventure adventure adventure adventure adventure adventure adventure"
    "schooling schooling business business adventure adventure adventure adventure adventure adventure adventure adventure"
    "schooling schooling equipment equipment adventure adventure adventure adventure adventure adventure adventure adventure"
    "research research equipment equipment adventure adventure adventure adventure adventure adventure adventure adventure"
    "research research training training adventure adventure adventure adventure adventure adventure adventure adventure"
    "research research training training adventure adventure adventure adventure adventure adventure adventure adventure"
    "current current current current current current current current current current current current"
    "planning planning planning planning planning planning planning planning planning planning planning planning"
    "planning planning planning planning planning planning planning planning planning planning planning planning"
    "action action action action action action action action action action action action"
    "action action action action action action action action action action action action"
}

.root > div {
  border: solid black 1px;
  overflow: scroll;
}

.header {
  grid-column-start: header-start;
  grid-column-end: header-end;
  grid-row-start: header-start;
  grid-row-end: header-end;
}

.schooling {
  grid-column-start: schooling-start;
  grid-column-end: schooling-end;
  grid-row-start: schooling-start;
  grid-row-end: schooling-end;
  overflow-y: scroll;
}

.business {
  grid-column-start: business-start;
  grid-column-end: business-end;
  grid-row-start: business-start;
  grid-row-end: business-end;
}

.research {
  grid-column-start: research-start;
  grid-column-end: research-end;
  grid-row-start: research-start;
  grid-row-end: research-end;
}

.equipment {
  grid-column-start: equipment-start;
  grid-column-end: equipment-end;
  grid-row-start: equipment-start;
  grid-row-end: equipment-end;
}

.training {
  grid-column-start: training-start;
  grid-column-end: training-end;
  grid-row-start: training-start;
  grid-row-end: training-end;
}

.adventure {
  grid-column-start: adventure-start;
  grid-column-end: adventure-end;
  grid-row-start: adventure-start;
  grid-row-end: adventure-end;
}

.current {
  grid-column-start: current-start;
  grid-column-end: current-end;
  grid-row-start: current-start;
  grid-row-end: current-end;
  overflow: initial !important;
}

.planning {
  grid-column-start: planning-start;
  grid-column-end: planning-end;
  grid-row-start: planning-start;
  grid-row-end: planning-end;
  display: flex;
  flex-direction: column;
}

.actions {
  grid-column-start: action-start;
  grid-column-end: action-end;
  grid-row-start: action-start;
  grid-row-end: action-end;
  overflow: initial !important;
}
