
.progress-container {
  min-width: 120px;
  max-width: 120px;
  height: 20px;
  border-top: solid black 1px;
  border-bottom: solid black 1px;
  border-right: solid black 1px;
}

.progress-container.active {
  background-color: lightgrey;
}

.effects-tooltip {
  visibility: hidden;
  background-color: #555;
  color: #fff;
  text-align: center;
  padding: 5px;
  border-radius: 6px;
  padding-right: 20px;
  text-align: left;

  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  margin-left: -50%;

  opacity: 0;
  transition: opacity 0.3s;
  white-space: pre;
}

.progress-container:hover .effects-tooltip {
  visibility: visible;
  opacity: 1;
}

.day-actions {
  text-align: left;
}

.container {
  text-align: center;
  position: relative;
}

.day-container {
  display: flex;
  overflow-x: scroll;
}

.day-container::-webkit-scrollbar {
  display: none;
}
