.action-title {
}

.action-button {
  display: inline-block;
  padding: 8px;
  margin: 4px;
}

.super-container {
  position: relative;
  flex: 1;
  border-right: solid black 1px;
  border-top: solid black 1px;
}

.action-container {
  flex: 1;
  text-align: center;
  height: calc((100vh / 6) - 14px);
  overflow: scroll;
  display: flex;
  flex-direction: column;
  visibility: hidden;
  pointer-events: auto;
}

.action-container.cat-unlocked {
  visibility: visible;
}

.button-container {
  position: relativve;
  margin-bottom: 3px;
}

.action-tooltip {
  visibility: hidden;
  background-color: #555;
  color: #fff;
  text-align: center;
  padding: 5px;
  border-radius: 6px;

  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  margin-left: -50%;

  opacity: 0;
  transition: opacity 0.3s;
  white-space: pre;
}

.super-container:hover .cat-locked .category-tooltip {
  visibility: visible;
  opacity: 1;
}

.action-tooltip::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.unlocked .action-button:hover .unlocked-tooltip {
  visibility: visible;
  opacity: 1;
}

.locked .action-button:hover .locked-tooltip {
  visibility: visible;
  opacity: 1;
}

.locked {
  opacity: .5;
}
